// components/GlobeStates.tsx
import React, {
  useEffect,
  useRef,
  useState,
  useMemo,
} from 'react';
import Globe from 'react-globe.gl';
import * as topojson from 'topojson-client';
import * as THREE from 'three';
import type { RegionId } from '../lib/jurisdictions';

interface GlobeStatesProps {
  region: RegionId;
  onStateSelect?: (
    id: string | number | null,
    name: string,
    coords?: { x: number; y: number },
  ) => void;
  onViewChange?: (altitude: number) => void;
  onRegionChange?: (region: RegionId) => void;
}

const regionFocus: Record<
  RegionId,
  { lat: number; lng: number; altitude: number }
> = {
  unitedStates: { lat: 39, lng: -98, altitude: 1.9 },
  canada: { lat: 62, lng: -96, altitude: 2.0 },
  mexico: { lat: 23, lng: -102, altitude: 2.0 },
  netherlands: { lat: 52.3, lng: 5.3, altitude: 2.4 },
  uruguay: { lat: -32.5, lng: -56, altitude: 2.4 },
  germany: { lat: 51.1, lng: 10.4, altitude: 2.4 },
  southAfrica: { lat: -30, lng: 25, altitude: 2.3 },
  luxembourg: { lat: 49.8, lng: 6.1, altitude: 2.5 },
  malta: { lat: 35.9, lng: 14.4, altitude: 2.5 },
  georgia: { lat: 42.3, lng: 43.4, altitude: 2.4 },
};

const usStatesUrl = 'https://unpkg.com/us-atlas@3/states-10m.json';
const worldCountriesUrl =
  'https://unpkg.com/world-atlas@2.0.2/countries-110m.json';

// Natural Earth / ISO 3166-1 numeric codes â†’ RegionId
// (These match the ids in the world-atlas topojson.)
const COUNTRY_ID_TO_REGION: Record<number, RegionId> = {
  840: 'unitedStates', // USA
  124: 'canada',
  484: 'mexico',
  528: 'netherlands',
  858: 'uruguay',
  276: 'germany',
  710: 'southAfrica',
  442: 'luxembourg',
  470: 'malta',
  268: 'georgia',
};

const GlobeStates: React.FC<GlobeStatesProps> = ({
  region,
  onStateSelect,
  onViewChange,
  onRegionChange,
}) => {
  const globeRef = useRef<any>(null);
  const [usPolygons, setUsPolygons] = useState<any[]>([]);
  const [worldPolygons, setWorldPolygons] = useState<any[]>([]);
  const [hoverPoly, setHoverPoly] = useState<any | null>(null);
  const [selectedStateId, setSelectedStateId] = useState<string | null>(null);
  const [selectedCountryNumericId, setSelectedCountryNumericId] = useState<
    number | null
  >(null);

  // Textures
  const [stainlessTex, setStainlessTex] = useState<THREE.Texture | null>(null);
  const [steelTex, setSteelTex] = useState<THREE.Texture | null>(null);
  const [blueTex, setBlueTex] = useState<THREE.Texture | null>(null);

  const isUS = region === 'unitedStates';

  // Helpers --------------------------------------------------------

  const getStatePostal = (poly: any): string | null => {
    const postal = poly?.properties?.postal ?? poly?.id;
    if (!postal) return null;
    return String(postal).toUpperCase();
  };

  const isState = (poly: any) =>
    !!poly?.properties && typeof poly.properties.postal !== 'undefined';

  const getCountryNumericId = (poly: any): number | null => {
    const raw = poly?.id;
    if (raw === null || raw === undefined) return null;
    if (typeof raw === 'number') return raw;
    const parsed = Number.parseInt(String(raw), 10);
    return Number.isNaN(parsed) ? null : parsed;
  };

  const isCountry = (poly: any) => {
    const hasId =
      poly?.properties && typeof getCountryNumericId(poly) === 'number';
    return hasId && !isState(poly);
  };

  const getCountryRegion = (poly: any): RegionId | null => {
    const idNum = getCountryNumericId(poly);
    if (idNum === null) return null;
    return COUNTRY_ID_TO_REGION[idNum] ?? null;
  };

  // Load topojson ---------------------------------------------------

  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(usStatesUrl);
        const topo = await res.json();
        const geo = topojson.feature(topo, topo.objects.states) as any;
        const states = geo.features.map((f: any) => ({
          ...f,
          properties: {
            ...f.properties,
            postal: f.properties?.postal || f.id,
          },
        }));
        setUsPolygons(states);
      } catch (err) {
        console.error('Error loading US states topojson', err);
      }
    })();
  }, []);

  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(worldCountriesUrl);
        const topo = await res.json();
        const geo = topojson.feature(topo, topo.objects.countries) as any;
        setWorldPolygons(geo.features);
      } catch (err) {
        console.error('Error loading world countries topojson', err);
      }
    })();
  }, []);

  // Textures --------------------------------------------------------

  useEffect(() => {
    const loader = new THREE.TextureLoader();

    loader.load('/textures/stainless.jpg', tex => {
      tex.wrapS = tex.wrapT = THREE.RepeatWrapping;
      setStainlessTex(tex);
    });

    loader.load('/textures/steel.jpg', tex => {
      tex.wrapS = tex.wrapT = THREE.RepeatWrapping;
      setSteelTex(tex);
    });

    loader.load('/textures/blue-metal.jpg', tex => {
      tex.wrapS = tex.wrapT = THREE.RepeatWrapping;
      setBlueTex(tex);
    });
  }, []);

  // Focus camera on active region ----------------------------------

  useEffect(() => {
    if (!globeRef.current) return;
    const focus = regionFocus[region];

    if (!focus) return;

    globeRef.current.pointOfView(
      {
        lat: focus.lat,
        lng: focus.lng,
        altitude: focus.altitude,
      },
      1000,
    );
  }, [region, usPolygons, worldPolygons]);

  // Altitude reporting ----------------------------------------------

  useEffect(() => {
    if (!globeRef.current || !onViewChange) return;

    const globe = globeRef.current;
    const handle = () => {
      const { altitude } = globe.pointOfView();
      onViewChange(altitude);
    };

    globe.controls().addEventListener('change', handle);
    return () => {
      globe.controls().removeEventListener('change', handle);
    };
  }, [onViewChange]);

  // Combined polygon data -------------------------------------------

  const allPolygons = useMemo(
    () => [...worldPolygons, ...usPolygons],
    [worldPolygons, usPolygons],
  );

  // Explosion / altitude settings -----------------------------------

  const BASE_ALT_STATE = 0.02;
  const BASE_ALT_COUNTRY = 0.014;
  const BASE_ALT_OTHER = 0.005;

  const EXTRA_SELECTED = 0.018;
  const EXTRA_REGION = 0.008;
  const EXTRA_HOVER = 0.006;

  const polygonAltitude = (poly: any) => {
    if (isState(poly)) {
      const id = getStatePostal(poly);
      const isSelected =
        id && selectedStateId && id === selectedStateId;
      const isHovered = hoverPoly === poly;

      let alt = BASE_ALT_STATE;
      if (isSelected) alt += EXTRA_SELECTED;
      if (isHovered) alt += EXTRA_HOVER;
      return alt;
    }

    if (isCountry(poly)) {
      const countryRegion = getCountryRegion(poly);
      const numericId = getCountryNumericId(poly);
      const isRegionCountry = countryRegion === region;
      const isSelectedCountry =
        numericId !== null &&
        selectedCountryNumericId !== null &&
        numericId === selectedCountryNumericId;
      const isHovered = hoverPoly === poly;

      let alt = BASE_ALT_COUNTRY;
      if (isRegionCountry) alt += EXTRA_REGION;
      if (isSelectedCountry) alt += EXTRA_SELECTED;
      if (isHovered) alt += EXTRA_HOVER;
      return alt;
    }

    return BASE_ALT_OTHER;
  };

  // Render ----------------------------------------------------------

  return (
    <div className="h-full w-full">
      <Globe
        ref={globeRef}
        backgroundColor="rgba(0,0,0,1)"
        animateIn
        hexPolygonResolution={3}
        hexPolygonMargin={0.4}
        globeMaterial={
          new THREE.MeshStandardMaterial({
            metalness: 0.9,
            roughness: 0.35,
            color: new THREE.Color('#020617'),
            envMap: stainlessTex ?? undefined,
            envMapIntensity: 1.0,
          })
        }
        polygonsData={allPolygons}
        polygonAltitude={polygonAltitude}
        polygonCapMaterial={poly => {
          // States: dark glossy tiles with green emission
          if (isState(poly)) {
            const id = getStatePostal(poly);
            const isSelected =
              id && selectedStateId && id === selectedStateId;
            const isHovered = hoverPoly === poly;

            const emissiveBase = new THREE.Color('#020617');
            const emissiveHighlight = new THREE.Color('#22c55e');

            return new THREE.MeshStandardMaterial({
              metalness: 0.96,
              roughness: 0.26,
              color: new THREE.Color('#020617'),
              emissive:
                isHovered || isSelected
                  ? emissiveHighlight
                  : emissiveBase,
              emissiveIntensity: isHovered
                ? 0.7
                : isSelected
                ? 0.5
                : 0.2,
              envMap: steelTex ?? undefined,
              envMapIntensity: isHovered || isSelected ? 1.4 : 1.0,
            });
          }

          // Countries: subtle metallic band; active jurisdiction glows a bit.
          if (isCountry(poly)) {
            const countryRegion = getCountryRegion(poly);
            const numericId = getCountryNumericId(poly);
            const isRegionCountry = countryRegion === region;
            const isSelectedCountry =
              numericId !== null &&
              selectedCountryNumericId !== null &&
              numericId === selectedCountryNumericId;

            const emissive =
              isRegionCountry || isSelectedCountry
                ? new THREE.Color('#22c55e')
                : new THREE.Color('#020617');

            const emissiveIntensity =
              isSelectedCountry ? 0.6 : isRegionCountry ? 0.35 : 0.18;

            return new THREE.MeshStandardMaterial({
              metalness: 0.8,
              roughness: 0.38,
              color: new THREE.Color('#020617'),
              emissive,
              emissiveIntensity,
              envMap: blueTex ?? undefined,
              envMapIntensity: 0.8,
            });
          }

          return new THREE.MeshStandardMaterial({
            metalness: 0.6,
            roughness: 0.45,
            color: new THREE.Color('#020617'),
          });
        }}
        polygonSideColor={poly =>
          isState(poly) || isCountry(poly)
            ? 'rgba(34,197,94,0.85)'
            : '#020617'
        }
        polygonStrokeColor={poly =>
          isState(poly) || isCountry(poly)
            ? 'rgba(21,94,49,0.5)'
            : 'rgba(30,64,175,0.25)'
        }
        polygonLabel={poly =>
          isState(poly)
            ? `${poly.properties?.name ?? ''} (${poly.properties?.postal})`
            : poly.properties?.name ?? ''
        }
        polygonsTransitionDuration={260}
        polygonCapCurvatureResolution={2}
        onPolygonHover={poly => {
          setHoverPoly(poly);
        }}
        onPolygonClick={(poly, event) => {
          const name = poly.properties?.name ?? '';

          const clickCoords =
            event && typeof (event as any).clientX === 'number'
              ? {
                  x: (event as any).clientX as number,
                  y: (event as any).clientY as number,
                }
              : undefined;

          if (isState(poly)) {
            const id = getStatePostal(poly) ?? poly.id ?? '';
            setSelectedStateId(id || null);
            setSelectedCountryNumericId(null);

            if (onStateSelect) {
              onStateSelect(id, name, clickCoords);
            }
            return;
          }

          if (isCountry(poly)) {
            const numericId = getCountryNumericId(poly);
            setSelectedStateId(null);
            setSelectedCountryNumericId(numericId);

            const regionFromCountry = getCountryRegion(poly);
            if (regionFromCountry && onRegionChange) {
              onRegionChange(regionFromCountry);
            }

            if (onStateSelect) {
              const id = name || poly.id || '';
              onStateSelect(id, name, clickCoords);
            }
          }
        }}
      />
    </div>
  );
};

export default GlobeStates;
